.App {
  font-family: "Fira Sans", sans-serif;
  text-align: center;
  // background-color: #ccc;
  background: center center / 100% fixed url(
   https://images.unsplash.com/photo-1562496146-d680d80e902b?crop=entropy&cs=tinysrgb&fit=crop&fm=jpg&h=720&ixid=eyJhcHBfaWQiOjF9&ixlib=rb-1.2.1&q=80&w=1080
    );

   // https://source.unsplash.com/random/1080x720/?computer
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
  color: white;
}

.App-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 60px;
  padding-right: 60px;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: fit-content;
  overflow: hide;
  margin: 3px;
  padding: 3px;
  border: 1px solid #34464f;
  border-radius: 2px;
  background-color: rgba(255,255,255,0.7);
}

table {
  width: fit-content;
  border: 1px solid #eaeaea;
  color: #000;
  background-color: white;
  padding: 8px;
  margin: 2px;
  font-size: 11px;
  td { border: 1px solid #eaeaea; padding: 24px; }
}

textarea {
  min-width: 300px;
  min-height: 140px;
  border: 1px solid gray;
  background-color: #303a3c;
  color: white;
  border-radius: 24px;
  padding: 22px;
  margin: 10px;
}

dt { font-weight: bold; margin-top: 8px; }
